@import "~@/assets/scss/production.scss";@import "~@/assets/scss/variables.scss";@import "~@/assets/scss/functions.scss";@import "~@/assets/scss/mixin.scss";
%common-icons {
  display: inline-block;
  vertical-align: middle;

  @include background-image(url(~@/assets/img/common/common_icons.png));
}

@include b(suspension) {
  position: fixed;
  right: 0;
  bottom: 20px;
  z-index: 9;
  margin-right: 20px;
  text-align: right;

  @include e(item) {
    display: inline-block;
    width: 168px;
    margin-top: 10px;
    overflow: hidden;
    font-size: 14px;
    line-height: 44px;
    color: #fff;
    white-space: nowrap;
    vertical-align: top;
    cursor: pointer;
    border-radius: 22px;
    transition: width 0.3s ease;

    &.open {
      border-radius: 10px;

      @include e(item-inner) {
        font-size: 12px;

        &.close {
          height: 30px;
          font-size: 22px;
          line-height: 26px;
          color: #a4a6a6;
          text-align: center;
          cursor: pointer;
          border-bottom: none;

          &:hover {
            color: #6e6e72;
            background-color: #39383f;
          }
        }
      }
    }

    &.open-service {
      @include e(item-inner) {
        line-height: 50px;
        text-align: left;
        border-bottom: 1px solid #4a494f;

        span {
          margin-left: 15px;
        }

        &:hover {
          background-color: #333239;
        }
      }
    }

    &.open-qr-code {
      cursor: default;

      @include e(item-inner) {
        padding: 0;
        line-height: 34px;
        color: #999;
        text-align: center;

        &:hover {
          background-color: #39383f;
        }
      }
    }
  }

  @include e(item-inner) {
    padding: 0 20px;
    overflow: hidden;
    text-align: right;
    background-color: #39383f;
    transition: all 0.2s ease;

    &:hover {
      background-color: #4e5165;
    }
  }

  @include e(icon-club) {
    display: inline-block;
    width: 100%;
    height: 106px;
    vertical-align: top;
    transition: all 0.3s ease;

    @include background-image(url(~@/components/suspension/img/club_max.png));
  }

  @include e(icon-download) {
    width: 8px;
    height: 12px;
    background-position: -149px -89px;

    @extend %common-icons;
  }

  @include e(icon-service) {
    width: 22px;
    height: 20px;
    background-position: -131px -25px;

    @extend %common-icons;
  }

  @include e(icon-phone) {
    width: 22px;
    height: 18px;
    background-position: -159px -25px;

    @extend %common-icons;
  }

  @include e(icon-tel) {
    width: 22px;
    height: 18px;
    background-position: -156px -49px;

    @extend %common-icons;
  }

  @include e(icon-tel-max) {
    width: 26px;
    height: 22px;
    background-position: -153px 0;

    @extend %common-icons;
  }

  @include e(icon-qr-code) {
    width: 22px;
    height: 22px;
    background-position: -181px -160px;

    @extend %common-icons;
  }

  @include e(icon-top) {
    display: inline-block;
    width: 44px;
    height: 44px;
    vertical-align: top;
    cursor: pointer;
    background: #fff no-repeat center;
    border-radius: 50%;
    opacity: 0.8;
    transition: all 0.3s ease;

    @include background-image(url(~@/components/suspension/img/to_top.png));

    &.hidden {
      visibility: hidden;
      opacity: 0;
    }
  }

  @include e(icon-gift) {
    display: inline-block;
    width: 86px;
    height: 86px;
    vertical-align: top;
    cursor: pointer;
    background: no-repeat left top/100% 100%;

    @include background-image(url(~@/components/suspension/img/gift.gif));
  }

  &.min {
    @include e(item) {
      &:not(.open) {
        width: 44px;

        @include e(item-inner) {
          padding: 0;
          text-align: center;
        }
      }
    }

    @include e(icon-club) {
      height: 44px;

      @include background-image(url(~@/components/suspension/img/club_min.png));
    }
  }
}
