@import "~@/assets/scss/production.scss";@import "~@/assets/scss/variables.scss";@import "~@/assets/scss/functions.scss";@import "~@/assets/scss/mixin.scss";
@include b(loading) {
  @include mask(999);

  @include e(logo) {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 83px;
    height: 83px;
    margin: auto;
    background: no-repeat left center;
    animation: rotate-bg 1s steps(12) infinite;

    @include background-image(url(~@/assets/img/components/loading/sprites.png));

    @keyframes rotate-bg {
      0% {
        background-position: 0 0;
      }

      100% {
        background-position: -996px 0;
      }
    }
  }

  &.enter {
    opacity: 0;
  }

  &.enter-active {
    opacity: 1;
    transition: all 0.3s ease;
  }

  &.exit {
    opacity: 1;
    transition: all 0.3s ease;
  }

  &.exit-active {
    opacity: 0;
  }
}

%position {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1;
}

@include b(page-loading) {
  @extend %position;

  background: #1b1b1b linear-gradient(180deg, #0a0a0a, #1b1b1b);
  transition: opacity 0.3s linear;

  @include e(inner) {
    @extend %position;

    width: calc(20px + 4vmin);
    height: calc(20px + 4vmin);
    margin: auto;
    overflow: hidden;
    transition: transform 0.3s linear;
    transform: scale(1);
  }

  @include e(circular) {
    width: 100%;
    height: 100%;
    animation: loading-rotate 2s linear infinite;
  }

  @include e(path) {
    stroke: #f5b607;
    stroke-dasharray: 90, 150;
    stroke-dashoffset: 0;
    stroke-linecap: round;
    stroke-width: 2;
    animation: loading-dash 1.5s ease-in-out infinite;
  }

  @keyframes loading-dash {
    0% {
      stroke-dasharray: 1, 200;
      stroke-dashoffset: 0;
    }

    50% {
      stroke-dasharray: 90, 150;
      stroke-dashoffset: -40px;
    }

    100% {
      stroke-dasharray: 90, 150;
      stroke-dashoffset: -120px;
    }
  }

  @keyframes loading-rotate {
    100% {
      transform: rotate(360deg);
    }
  }
}

@include b(rotate) {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 34px;
  height: 34px;
  margin-top: -17px;
  margin-left: -17px;
  overflow: hidden;
  background: no-repeat center/14px auto;

  @include background-image(url(~@/assets/img/common/rotate_logo.png));

  @include e(img) {
    width: 100%;
    height: auto;
    vertical-align: top;
    animation: rotate 2s linear infinite;
  }

  @keyframes rotate {
    0% {
      transform: rotate(0);
    }

    100% {
      transform: rotate(360deg);
    }
  }
}
