@import "~@/assets/scss/production.scss";@import "~@/assets/scss/variables.scss";@import "~@/assets/scss/functions.scss";@import "~@/assets/scss/mixin.scss";
@include b(banner) {
  height: 100%;
  min-height: 300px;

  @include e(item) {
    overflow: hidden;

    &.cursor {
      cursor: pointer;
    }
  }

  @include e(img) {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  :global(.swiper-pagination-bullet) {
    width: 12px;
    height: 12px;
    background-color: transparent;
    border: 1px solid #666;
    border-radius: 6px;
    opacity: 1;
    transition: border 0.5s ease 0.5s, background-color 0.5s ease 0.5s, width 0.5s ease;
  }

  :global(.swiper-pagination-bullet-active) {
    width: 24px;
    background-color: $--color-primary;
    border: 1px solid transparent;
    transition-delay: 0s;
  }
}
