@import "~@/assets/scss/production.scss";@import "~@/assets/scss/variables.scss";@import "~@/assets/scss/functions.scss";@import "~@/assets/scss/mixin.scss";
@include b(qr-code) {
  position: relative;
  box-sizing: content-box;
  width: 124px;
  height: 124px;
  padding: 10px;
  margin: auto;
  font-size: 0;
  background-color: #fff;

  img {
    width: 100%;
    height: 100%;
  }

  @include e(logo) {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 28px !important;
    height: 28px !important;
    margin-top: -14px;
    margin-left: -14px;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.8);
  }
}
