@import "~@/assets/scss/production.scss";@import "~@/assets/scss/variables.scss";@import "~@/assets/scss/functions.scss";@import "~@/assets/scss/mixin.scss";
%footer-inner {
  width: $--content-width;
  margin: 0 auto;
  overflow: hidden;
}

@include b(footer) {
  background: url(~@/components/footer/img/bottom_bg.png) no-repeat center bottom, #1b1b1b linear-gradient(180deg, #0a0a0a, #1b1b1b);
  border-top: 1px solid $--color-primary;

  @include e(inner) {
    @extend %footer-inner;
  }

  @include e(nav) {
    margin-top: 24px;
  }

  @include e(nav-item) {
    display: inline-block;
    width: 300px;
    vertical-align: top;
  }

  @include e(nav-icon) {
    display: inline-block;
    width: 60px;
    height: 60px;
    margin-right: 16px;
    vertical-align: top;

    @include background-image(url(~@/components/footer/img/icons.png));

    @for $i from 2 through 4 {
      &.icon-#{$i} {
        background-position: ($i - 1) * -60px 0;
      }
    }
  }

  @include e(nav-content) {
    display: inline-block;
    font-size: 12px;
    vertical-align: top;
  }

  @include e(nav-title) {
    margin-top: 7px;
    font-size: 16px;
    line-height: 22px;
    color: #ccc;
  }

  @include e(nav-subtitle) {
    margin-top: 4px;
    line-height: 18px;
    color: #666;
  }

  @include e(nav-link-wrapper) {
    margin-top: 10px;
    color: $--color-primary;
  }

  @include e(nav-link) {
    transition: color 0.2s ease;

    & + & {
      margin-left: 8px;
    }

    &:hover {
      color: rgba(203, 163, 82, 0.95);
      text-decoration: underline;
    }
  }

  @include e(partners) {
    position: relative;
    height: 70px;
    margin-top: 30px;
    overflow: hidden;
    background: no-repeat center;
    border-top: 1px solid #2a2c31;
    border-bottom: 1px solid #2a2c31;

    @include background-image(url(~@/components/footer/img/logos.png));
  }

  @include e(img) {
    position: absolute;
    top: 0;
    left: 50%;
    margin-left: -(1920px / 2);
  }

  @include e(link-wrapper) {
    margin-top: 20px;
    font-size: 14px;
    line-height: 20px;
    color: #666;
    text-align: center;
  }

  @include e(link) {
    position: relative;
    padding: 0 20px;
    transition: color 0.2s ease;

    & + & {
      &::before {
        position: absolute;
        top: 5px;
        left: 0;
        width: 1px;
        height: 10px;
        content: '\20';
        border-left: 1px solid #666;
      }
    }

    &:hover {
      color: $--color-primary;
      text-decoration: underline;
    }
  }

  @include e(copyright) {
    margin: 8px 0;
    font-size: 12px;
    line-height: 16px;
    color: #484848;
    text-align: center;
  }

  &.padding-bottom-games {
    padding-bottom: 90px;
  }
}

@include b(footer-promo) {
  background: #1b1b1b linear-gradient(180deg, #0a0a0a, #1b1b1b);
  border: none;

  @include e(inner) {
    @extend %footer-inner;

    height: 60px;
    line-height: 60px;
  }

  @include e(link) {
    padding: 0 12px;
  }

  @include e(button) {
    width: 104px;
    height: 35px;
    font-size: 14px;
    line-height: 35px;
    color: #666;
    text-align: left;
    border: 1px solid #3f3f3f;
    border-radius: 4px;

    &:hover {
      color: #fff;

      @include button-background-solid(false);

      @include e(icon) {
        @include background-image(url(~@/components/footer/img/service_active.png));
      }
    }
  }

  @include e(icon) {
    display: inline-block;
    width: 22px;
    height: 25px;
    margin: 4px 7px 0;
    vertical-align: top;

    @include background-image(url(~@/components/footer/img/service.png));
  }

  @include e(link-wrapper) {
    float: right;
  }
}
